.back {
    background-color: #0d5432;
    height: 150px;
    width: 1000px;
    position: absolute;
    top: 15px;
    margin-left: 170px;
    box-shadow: 20px 0 50px -4px rgba(0, 0, 0, 0.8),
        -20px 0 50px -4px rgba(0, 0, 0, 0.8);
}

.top {
    background: linear-gradient(to right, white, #2ca66b);
    width: 100%;
    height: 100px;
    display: flex;
}

.top>img {
    margin-top: 10px;
    margin-left: 15px;
    height: 70px;
    width: auto;
}

.Img2>img {
    margin-left: 50px;
    margin-top: 10px;
}

.bar {
    height: 50px;
    width: 100%;
    position: relative;
    background: linear-gradient(#104e07, #031001);
}

.bar>ul {
    list-style: none;
    display: inline-block;
    padding-left: 0px;
    margin-top: 17.5px;

}

.bar>ul>a {
    text-decoration: none;
    padding: 17px 14px 14px 17px;
    border-right: 1px solid white;
    cursor: pointer;
    color: white;
    font-weight: 600;
    font-family: Arial, Helvetica, sans-serif
}

.bar>ul>a:hover {
    background-color: #133f0c;
}

* {
    margin: 0%;
    padding: 0%;
}

.background {
    background-color: #01220d;
    height: 135px;
    width: 100%;
}

.background2 {
    background: linear-gradient(#146b41, white);
    height: 500px;
    width: 100%;
}

.background3 {
    background-color: white;
    height: 100vh;
    width: 100%;
}

.background2>img {
    height: 85px;
    width: auto;
    position: fixed;
    bottom: 20px;
    margin-left: 10px;
}

.background2>.what {
    right: 0px;
    margin-bottom: 350px;
    cursor: pointer;
}

.background2>.what {
    animation-name: moving;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease;
}

@keyframes moving {
    0% {
        transform: translateY(0px);
    }

    50% {
        transform: translateY(-150px)
    }

    100% {
        transform: translateY(0px);
    }
}

